<template>
  <div id="showBlog">
      <input type="text" v-model="search" placeholder="搜索框">
      <div class="show" v-for="sf in searchFilter">

        <router-link v-bind:to="'/blog/'+ sf.id">
          <h1>{{sf.blogTitle | to-uppercase}}</h1>
        </router-link>
        <p>{{sf.blogContent | snippet}}</p>
      </div>

  </div>
</template>

<script>
export default {
  name: 'showBlog',
  data () {
    return {
      blogShowObj:{},
      blogShowArrayKey:[],
      blogShowObjValue:[],
      search:"",
    }
  },
  created(){
    this.$http.get("https://wd8633350856oxekyx.wilddogio.com/myBlog2/post.json").then(function(data){return data.json()}).
    then(function(data){for(let key in data){

      this.blogShowObj=data;
      this.blogShowArrayKey.push(key);
      this.blogShowObjValue.push(data[key]);
      data[key].id=key;
    }})
  },
  computed:{
    searchFilter:function(){
    return this.blogShowObjValue.filter((value)=>{
                                           return value.blogTitle.match(this.search);
                                         })

    }
  }
}
</script>


<style scoped>
#showBlog{
    margin:20px auto;
    max-width:600px;
    padding:20px;
    background:blue;
    text-align:center;
  }

    input[type="text"]{
      width:100%;
      height:100px;
      font-size:50px;
    }
    .show{
      background:yellow;
    }
</style>



