<template>
  <div id="editBlog">
      <form>
        <label>博客标题</label>
        <input type="text" v-model="blogTitle">
        <label>博客内容</label>
        <textarea v-model="blogContent"></textarea>
      </form>
      <button v-on:click="post()">提交保存</button>
  </div>
</template>

<script>
export default {
  name: 'editBlog',
  data () {
    return {
      id:this.$route.params.id,
      data:{},
      blogTitle:"",
      blogContent:"",
    }
  },
  created(){
      this.$http.get("https://wd8633350856oxekyx.wilddogio.com/myBlog2/post/"+this.id+".json").then(function(data){ return data.json()}).then(function(data){

        for(let key in data){this.blogTitle=data.blogTitle;this.blogContent=data.blogContent;}
      })
    },
  methods:{
    post:function(){
             this.$http.put("https://wd8633350856oxekyx.wilddogio.com/myBlog2/post/"+this.id+".json",{
                 blogTitle:this.blogTitle,
                 blogContent:this.blogContent,
             }).then(response=>this.$router.push({path:"/"}))
           }
  }
}
</script>


<style scoped>
#editBlog{
    margin:20px auto;
    max-width:600px;
    padding:20px;
    background:blue;
    text-align:center;
  }
  label{
  display:block;
  text-align:center;
  font-size:100px;
  }
  input[type="text"],textarea{
    width:100%;
  }
  input[type="text"]{
    height:100px;
    font-size:50px;
  }
  textarea{
    height:400px;
  }
  button{
      height:100px;
      font-size:50px;
      width:50%;
      margin: auto;
      font-size:400%;
      font-weight:bold;
  }
</style>



