<template>
  <div id="blogDetail">
      <div>
              <h1 id="detailPage">详情页</h1>
              标题：<h1 class="title">{{data.blogTitle}}</h1>
              内容：<p>{{data.blogContent}}</p>
              <router-link v-bind:to="'/edit/'+id">编辑</router-link>
              <button @click="deleteBlog()">删除</button>
      </div>
  </div>
</template>

<script>
export default {
  name: 'blogDetail',
  data () {
    return {
      id:this.$route.params.id,
      data:{},
    }
  },
  created(){
    this.$http.get("https://wd8633350856oxekyx.wilddogio.com/myBlog2/post/"+this.id+".json").then(function(data){ return data.json()}).then(function(data){
      for(let key in data){this.data=data;}
    })
  },
  methods:{
    deleteBlog:function(){this.$http.delete("https://wd8633350856oxekyx.wilddogio.com/myBlog2/post/"+this.id+".json")
    .then(
     response=>this.$router.push({path:"/"})
    )}
  }
}
</script>


<style scoped>
#blogDetail{
    margin:20px auto;
    max-width:600px;
    padding:20px;
    background:gray;
    text-align:center;
    font-size:50px;
    font-weight:bold;
  }
 #detailPage{
    color:blue;
    font-size:80px;
 }
 .title{
  font-weight:normal;
  font-size:50px;
  background:white;
  word-wrap: break-word;
 }
 p{
  font-weight:normal;
  word-wrap: break-word;
  background:white;
 }
</style>



