<template>
  <div id="writeBlog">
      <form>
        <label>标题</label>
        <input type="text" v-model="blogTitle">
        <label>内容</label>
        <textarea v-model="blogContent"></textarea>
      </form>
      <button v-on:click="post()">提交保存</button>
  </div>
</template>

<script>
export default {
  name: 'writeBlog',
  data () {
    return {
      blogTitle:"",
      blogContent:"",
    }
  },
  methods:{
    post:function(){
             this.$http.post("https://wd8633350856oxekyx.wilddogio.com/myBlog2/post.json",{
                 blogTitle:this.blogTitle,
                 blogContent:this.blogContent,
             }).then(response=>this.$router.push({path:"/"}))
           }
  }
}
</script>


<style scoped>
#writeBlog{
    margin:20px auto;
    max-width:600px;
    padding:20px;
    background:blue;
    text-align:center;
  }
  label{
  display:block;
  text-align:center;
  font-size:100px;
  }
  input[type="text"],textarea{
    width:100%;
  }
  input[type="text"]{
    height:100px;
    font-size:50px;
  }
  textarea{
    height:400px;
  }
  button{
      height:100px;
      font-size:50px;
      width:50%;
      margin: auto;
      font-size:400%;
      font-weight:bold;
  }
</style>



